import React, { Component }  from 'react';
import "./App.css";
import Header from '../Header/Header';
import Quiz from '../Quiz/Quiz';
import BirdsList from '../BirdsList/BirdsList';
import Bird from '../Bird/Bird';

export default class App extends Component { 
  state = {
    random: 0,
    page: 0,
    id: 0,
    select: false,
    win: false,
    score: 0,
    step: 0
  }

  componentDidMount() {
    this.setState({
      random: this.randomBird()
    });
  }

  randomBird = () => {
    return Math.floor(Math.random() * 6);
  }

  onBirdSelected = (id, event) => {
    event.persist();
    this.setState((prevState) => ({
      id: id - 1,
      select: true,
      step: prevState.step + 1
    }));
    this.getWin(id);
    this.styleListItem(event);  
  };

  styleListItem = (event) => {
    if(event._targetInst.key - 1 === this.state.random && !this.state.win) { 
      event.target.classList.add('success')
    } else if(event._targetInst.key - 1 !== this.state.random && !this.state.win) {  
      event.target.classList.add('error')
    }  
  }

  getWin = (id) => {
    if(id - 1 !== this.state.random || this.state.win) return;
    this.setState((prevState) => ({
      score: prevState.score + 5 - this.state.step,
      win: true
    }));
  }

  getNextPage = () => {
    if(this.state.page === 5 || !this.state.win) return;
    this.setState((prevState) => ({       
      random: this.randomBird(),
      page: prevState.page + 1,
      select: false,
      win: false,
      step: 0
    }));
  }  
  
  render() {
    return (
      <>
        <Header 
          page={this.state.page}
          score={this.state.score} />
        <Quiz 
          random={this.state.random} 
          page={this.state.page}
          win={this.state.win} />
        <div className="row mb2">
          <BirdsList
            onItemSelected={this.onBirdSelected}
            page={this.state.page} />
          <Bird 
            id={this.state.id} 
            page={this.state.page}
            select={this.state.select} />
          <button
            className="btn btn-secondary"
            style={{backgroundColor: this.state.win ? '#00efb2' : '#303030', cursor: this.state.win ? 'pointer' : 'auto'}}
            onClick={this.getNextPage}>
              Next
          </button>
        </div>
      </>
    )
  }
}